.cardborder {
    background-color: #000000;
    margin: 2px;
}

.buttonborder {
    background-color: #333a41;
    /* border-color: #000000; */
    border-color: #ff0000;
    color: white;

    font-size: 14px;
    padding: 6px 12px;
    margin-bottom: 10;

    display: inline-block;
    /* text-decoration: none; */
    text-align: center;
    white-space: nowrap;
    vertical-align: middle;
    -ms-touch-action: manipulation;
    touch-action: manipulation;
    cursor: pointer;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    background-image: none;
    /* border: 1px solid transparent; */
    border: 20px;
    border-radius: 4px;
}
.buttonborder:focus,
.buttonborder:active:focus {
    outline: thin dotted;
    outline: 5px auto -webkit-focus-ring-color;
    outline-offset: -2px;
}
.buttonborder:hover,
.buttonborder:focus {
    color: #000000;
    text-decoration: none;
}
.buttonborder:active {
    background-image: none;
    outline: 0;
    -webkit-box-shadow: inset 0 3px 5px rgba(0, 0, 0, .125);
    box-shadow: inset 0 3px 5px rgba(0, 0, 0, .125);
}